<template>
    <view>
        <m-common title="加载页" />
        <m-cell-group border>
            <m-cell
                title="基本使用"
                is-link
                @click="handleClick({text: '正在加载'})"
            />
            <m-cell
                title="自定义提示文字"
                is-link
                @click="handleClick({text: 'loading...'})"
            />
            <m-cell
                title="动画模式"
                is-link
                @click="handleClick({mode: 'spinner'})"
            />
        </m-cell-group>
        <m-loading-page
            :loading="loading"
            :loading-text="title"
            :loading-mode="loadingMode"
        />
    </view>
</template>

<script lang="ts" setup>
import { ref } from 'vue'
const loading = ref(false)
const title = ref('正在加载')
const loadingMode = ref('')

const handleClick = (conf = {}) => {
    const { text = '正在加载', mode = 'circle' } = conf
    loading.value = true
    title.value = text
    loadingMode.value = mode

    setTimeout(() => {
        loading.value = false
    }, 1000)
}
</script>

